import React, { PureComponent } from "react";
import "./Search.css";
import { Link } from "react-router-dom";

export default class Search extends PureComponent {
    render() {
        return (
            <div style={{padding:"2vw", maxWidth: 800, margin: "0 auto"}}>
                <h1 id="logo" className="text-center">
                    <span style={{color: '#4285F4', margin: '0.1em'}}>S</span>
                    <span style={{color: '#EA4335', margin: '0.1em'}}>A</span>
                    <span style={{color: '#FBBC05', margin: '0.1em'}}>R</span>
                    <span style={{color: '#4285F4', margin: '0.1em'}}>A</span>
                </h1>
                <div className="input-group" style={{padding: '2vw', outline: 'none'}}>
                    <input type="text" className="form-control" aria-label="Amount (to the nearest dollar)"/>
                    <div className="input-group-append">
                        <select id="select-search" className="bootstrap-select btn" style={{backgroundColor: '#FBC117', color: 'rgba(0,0,0,0);', outline: 'none'}}>
                            <option value="1" selected="selected">CSV</option>
                            <option value="2">JSON</option>
                            <option value="3">XML</option>
                            <option value="4">Google</option>
                        </select>
                        <span id="search-button" className="btn" style={{backgroundColor: "#4285F4"}}><i style={{color: 'white'}} className="fa fa-search" /></span>
                    </div>
                </div>
            </div>

        )
    }
}