import React, { PureComponent } from "react";

export default class About extends PureComponent {

    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className={"col-12 col-md-12 browser-item"}>
                        <div className="card bg-light mb-3">
                            <div className="card-header text-center"><h3>About</h3></div>
                            <div className="card-body">
                                <p className="card-text">
                                    <h5 className={"card-title"}>The Developer </h5>
                                    <em>Hui Lin is a student at Queens College who built this website for his computer science class with Professor Teitelman.</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>Contact Us</h5>
                                    <em>You can reach Hui Lin at huilin1618@gmail.com.</em>
                                </p>
                            </div>
                        </div>
                </div>
            </div>
            </div>
        )
    }
}