import React, { PureComponent } from "react";
import { Link } from "react-router-dom";

export default class Navbar extends PureComponent {
  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-dark bg-primary" style={{marginBottom: "3vh"}}>
        <Link style={{textDecoration: 'none'}} to="/">
          <a className="navbar-brand" href="/">S.A.R.A.</a>
        </Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item dropdown">
              <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                Course
              </a>
              <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                <a className="dropdown-item" target="_blank" href="https://learn.zybooks.com/zybook/CUNYCSCI355TeitelmanFall2019">Zybook</a>
                <a className="dropdown-item" target="_blank" href="https://app.tophat.com/e/427895/lecture">TopHat</a>
                <a className="dropdown-item" target="_blank" href="https://tinyurl.com/CSCI355-Fall2019">Google Drive</a>
                <div className="dropdown-divider"></div>
                <a className="dropdown-item" target="_blank" href="https://www.w3schools.com/">w3schools</a>
              </div>
            </li>
            <li className="nav-item">
              <Link style={{textDecoration: 'none'}} to="/">
                <a className="nav-link">Search <span className="sr-only">(current)</span></a>
              </Link>
            </li>
            <li className="nav-item">
              <Link style={{textDecoration: 'none'}} to="/browser">
                <a className="nav-link">Browser <span className="sr-only">(current)</span></a>
              </Link>
            </li>
            <li className="nav-item">
              <Link style={{textDecoration: 'none'}} to="/about">
                <a className="nav-link">About <span className="sr-only">(current)</span></a>
              </Link>
            </li>
          </ul>
        </div>
      </nav>
    )
  }
}