import React, { PureComponent } from "react";
import "./Browser.css";
import BrowserSubitem from "./BrowserSubitem";
import { Link } from "react-router-dom";

export default class Browser extends PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            latitude: 0,
            longitude: 0
        }
    }

    componentDidMount() {
        navigator.geolocation.getCurrentPosition(position => {
            this.setState({
                latitude: position.coords.latitude,
                longitude: position.coords.longitude
            })
        })
    }

    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className={"col-12 col-md-3 browser-item"}>
                        <div className="card bg-light mb-3">
                            <div className="card-header"><h3>Navigator</h3></div>
                            <div className="card-body">
                                <p className="card-text">
                                    <h5 className={"card-title"}>appName </h5>
                                    <em>{navigator.appName}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>product </h5>
                                    <em>{navigator.product}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>appVersion </h5>
                                    <em>{navigator.appVersion}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>userAgent </h5>
                                    <em>{navigator.userAgent}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>platform </h5>
                                    <em>{navigator.platform}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>language </h5>
                                    <em>{navigator.language}</em>
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className={"col-12 col-md-3 browser-item"}>
                        <div className="card bg-light mb-3">
                            <div className="card-header"><h3>Window</h3></div>
                            <div className="card-body">
                                <p className="card-text">
                                    <h5 className={"card-title"}>innerHeight </h5>
                                    <em>{window.innerHeight}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>innerWidth </h5>
                                    <em>{window.innerWidth}</em>
                                </p>
                            </div>
                        </div>
                        <div className="card bg-light mb-3">
                            <div className="card-header"><h3>Geolocation</h3></div>
                            <div className="card-body">
                                <p className="card-text">
                                    <h5 className={"card-title"}>latitude </h5>
                                    <em>{this.state.latitude}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>longitude </h5>
                                    <em>{this.state.longitude}</em>
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className={"col-12 col-md-3 browser-item"}>
                        <div className="card bg-light mb-3">
                            <div className="card-header"><h3>Screen</h3></div>
                            <div className="card-body">
                                <p className="card-text">
                                    <h5 className={"card-title"}>width </h5>
                                    <em>{window.screen.width}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>height </h5>
                                    <em>{window.screen.height}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>availWidth </h5>
                                    <em>{window.screen.availWidth}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>availHeight </h5>
                                    <em>{window.screen.availHeight}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>colorDepth </h5>
                                    <em>{window.screen.colorDepth}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>pixelDepth </h5>
                                    <em>{window.screen.pixelDepth}</em>
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className={"col-12 col-md-3 browser-item"}>
                        <div className="card bg-light mb-3">
                            <div className="card-header"><h3>Location</h3></div>
                            <div className="card-body">
                                <p className="card-text">
                                    <h5 className={"card-title"}>href </h5>
                                    <em>{window.location.href}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>hostname </h5>
                                    <em>{window.location.hostname}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>pathname </h5>
                                    <em>{window.location.pathname}</em>
                                </p>
                                <p className="card-text">
                                    <h5 className={"card-title"}>protocol </h5>
                                    <em>{window.location.protocol}</em>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}